import { map, slice, filter, some, toLower, trim } from 'lodash'

import GoogleSheets from './GoogleSheets'
import { mapCourthouse, mapCourts } from './sheetsMappers'

const friendlyEq = (v1, v2) => toLower(trim(v1)) === toLower(trim(v2))

const courtsService = {
  async getAllCourthouses() {
    const val = await GoogleSheets.getSheet(process.env.REACT_APP_GOOGLE_COURTHOUSES_SHEET_ID, process.env.REACT_APP_GOOGLE_COURTHOUSES_SHEET_NAME)

    return map(slice(val.values, 2), mapCourthouse)
  },

  async getAllCourts() {
    const val = await GoogleSheets.getSheet(process.env.REACT_APP_GOOGLE_COURTS_SHEET_ID, process.env.REACT_APP_GOOGLE_COURTS_SHEET_NAME)

    return map(slice(val.values, 2), mapCourts)
  },

  async findCourtsByCounty(countySearch) {
    const courts = await this.getAllCourts()

    return filter(courts, ({ county }) => friendlyEq(countySearch, county))
  },

  async findCourthousesByCounty(countySearch) {
    const courthouses = await this.getAllCourthouses()

    return filter(courthouses, ({ county }) => friendlyEq(countySearch, county))
  }
}

window.courtsService = courtsService

export default courtsService
