import React from 'react';
import { map } from 'lodash'
import CourtCard from '../molecules/CourtCard'
import { startScrollTracking } from '../../utils/analytics';

var resultsFirstRendered = false

function ResultList({ items, onMattersServedClick }) {
  if (items && items.length > 0 && !resultsFirstRendered) {
    resultsFirstRendered = true
    startScrollTracking()
  }

  return (
    <ul className="list-unstyled">
      { map(items, (item) => (
          <li key={item.courthouse_name} className="mb-4">
            <CourtCard item={item} onMattersServedClick={onMattersServedClick} />
          </li>
        ))
      }
    </ul>
  )
}

export default ResultList
