import React from 'react'
import { groupBy, map, mapValues, filter, pickBy, isEmpty, trim } from 'lodash'
import Autolinker from 'autolinker'
import PhoneNumber from '../../atoms/PhoneNumber'
import { getGoogleMapURL } from '../../../utils/courthouses'
import { sendAnalyticsClick } from '../../../utils/analytics'

const DAYS = ['M', 'Tu', 'W', 'Thu', 'F', 'Sa', 'Su']

// Check every item in the array to see if its a one higher number than the
// previous item — aka: [0,1,2], [4,5], [2,3,4], etc.
// [0, 2], [2,3,5], etc. would *not* be considered sequential
const isSequential = (arr) => arr.every((num, i) => i === arr.length - 1 || num == arr[i + 1] - 1)

// Renders either M
// or M-F
// or M,W,F
// etc.
const idxToDayName = (arr) => {
  if (arr.length === 1)
    return DAYS[arr[0]]
  else if (isSequential(arr))
    return `${DAYS[arr[0]]}-${DAYS[arr[arr.length-1]]}`
  else
    return map(arr, (v) => DAYS[v]).join(', ')
}

const isValidTime = (value, key) => key !== 'N/A' && key !== '?' && trim(key) !== ''

const renderHours = (arr) => {
  const hoursWithIdx = map(arr, (v, idx) => [ idx, v])
  const hoursGrouped = pickBy(groupBy(hoursWithIdx, ([idx, v]) => v), isValidTime)
  const hoursGroupedWithIdx = mapValues(hoursGrouped, (values, key) => {
      return map(values, ([idx, v]) => idx)
    }
  )

  if (isEmpty(hoursGroupedWithIdx))
    return null

  return (
    <div className='hours'>
    {
      map(hoursGroupedWithIdx, (values, key) => {
        const textWithLinks = Autolinker.link(key)
        return <div>{idxToDayName(values)}: <div className='hours-dates' dangerouslySetInnerHTML={{__html: textWithLinks}} /></div>
      })
    }
    </div>
  )
}

function HoursTable({ item }) {
  const building = renderHours(item.hours_building)
  const clerk = renderHours(item.hours_clerk_filing)
  const assistance = renderHours(item.hours_self_help_resource_center)
  return (
    <div className='court-card-hours row'>
      { !isEmpty(building) && (
      <div className='col-md-4'>
        <label>Building</label>
        <br></br>
        {building}
      </div>
      )}
      { !isEmpty(clerk) && (
      <div className='col-md-4'>
        <label>Clerks</label>
        <br></br>
        {clerk}
      </div>
      )}
      { !isEmpty(assistance) && (
        <div className='col-md-4'>
          <label>Self-Help Assistance</label>
          <br></br>
          {assistance}
        </div>
      )}
    </div>
  )
}

function TagList({ tags }) {
  return (
    map(tags, (t,idx) => (
      <React.Fragment key={`tag-${idx}`}>
        <span key={t} className='badge badge-primary mr-1'>{t}</span>
      </React.Fragment>
    )))
}

//{ <FontAwesomeIcon icon={['fas','info-circle']} /> }
function Description({ item, onMattersServedClick }) {
  const tags = item.matters_served
  let courthouseName = item.courthouse_name
  if (!isEmpty(item.courthouse_page_url)) {
    courthouseName = <a target="_blank" rel="noopener noreferrer" href={item.courthouse_page_url}>{courthouseName}</a>
  }
  const address = item.address.split(/\n/)
  const parking = Autolinker.link(item.parking || "")
  const transportation = Autolinker.link(item.transportation || "")

  return (
    <React.Fragment>
      <div className='row mt-3'>
        <div className='col-md-8 col-sm-12'>
          <h3 className="court-card-title">{courthouseName}</h3>
          <div className='matters-served' onClick={onMattersServedClick}>
            <span className='matters-served-label'>Matters Served</span>
            <span className='matters-served-link'>What does this mean?</span>
          </div>
          <div>
            {<TagList tags={tags} />}
          </div>
        </div>
        <div className='col-sm-12 col-md-4 contact-info'>
          { map(address, (value) => <div>{value}</div>)}
          <div><a aria-label={"google map of " + item.courthouse_name + " courthouse"} target="_blank" rel="noopener noreferrer" href={getGoogleMapURL(item.address)} onClick={event => sendAnalyticsClick('Outbound Link - Get Directions', event.target.href)}>Get Directions</a></div>
          <PhoneNumber value={item.phone_number} />
        </div>
      </div>
      <hr className='court-card-description-break' />
      <div className='row court-card-description'>
        <div className='col-12'>
          { (!isEmpty(parking) && parking != 'N/A') && <p className='court-card-info'><strong>Parking:</strong> <span dangerouslySetInnerHTML={{__html: parking}} /></p> }
          { (!isEmpty(transportation) && transportation != 'N/A') && <p className='court-card-info'><strong>Transportation:</strong> <span dangerouslySetInnerHTML={{__html: transportation}} /></p> }
        </div>
        <div className='col-12 col-md-12'>
          <div className='row'>
            <div className='col-12'>
              <label className='court-card-info'><strong>Hours</strong></label>
            </div>
          </div>
          <div className='row'>
            <div className='col-12'>
              <HoursTable item={item} />
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default Description
