import React from 'react'
import { trim } from 'lodash'

const parseNumber = (value) => {
  const match = value.split('-')

  if (match.length === 3) {
    return `(${trim(match[0])}) ${trim(match[1])}-${trim(match[2])}`
  }
  else if (match.length === 2) {
    return `${trim(match[0])}-${trim(match[1])}`
  }

  return ''
}

const numberLink = (value) => {
  return "tel:" + trim(value.replace(/[^0-9a-zA-Z]+/g, ''))
}

function PhoneNumber({ value }) {
  return (
    <div><a href={numberLink(value)}>{parseNumber(value)}</a></div>
  )
}

export default PhoneNumber
