import { map, slice, toLower, trim, capitalize, split, flow, isEmpty, filter } from 'lodash'

const getTimes = (entry, stidx) => slice(entry, stidx, stidx + 5)

const parseMattersServed = (ms) => map(split(ms, ','), flow(trim))

const isValidTime = (value) => value && value !== 'N/A'

const fortifyCourthouse = (c) => {
  return {
    ...c,
    has_in_person_help: !isEmpty(filter(c.hours_self_help_resource_center, isValidTime))
  }
}

export const mapCourthouse = (entry) => {
  return fortifyCourthouse({
    county: trim(toLower(entry[0])),
    courthouse_name: entry[1],
    address: entry[2],
    phone_number: entry[3],
    courthouse_page_url: entry[4],
    matters_served: parseMattersServed(entry[5]),
    hours_building: getTimes(entry, 6),
    hours_clerk_filing: getTimes(entry, 11),
    hours_self_help_resource_center: getTimes(entry, 16),
    parking: entry[21],
    transportation: entry[22],
    map: entry[23],
    photo: entry[24]
  })
}

export const mapCourts = (entry) => {
  return {
    county: trim(toLower(entry[0])),
    website_url: entry[1],
    self_help_url: entry[6],
    matters: entry[10]
  }
}
