import React, { Component } from 'react'
import { library } from '@fortawesome/fontawesome-svg-core'
import queryString from 'query-string'
import { faLandmark, faInfoCircle, faInfo } from '@fortawesome/free-solid-svg-icons'
import { startCase, isEmpty } from 'lodash'

import Search from './containers/Search'
import ChoseCounty from './components/molecules/ChoseCounty'
import CourtsService from './services/CourtsService'
import { findCounties, cleanQuery } from './utils/courthouses'
import cicozip from './data/cicozip.json'

import './App.scss'

library.add(faLandmark, faInfoCircle, faInfo)

class App extends Component {
  state = {}

  componentDidMount() {
    const parsedQs = queryString.parse(window.location.search)
    const query = cleanQuery(parsedQs.query)
    const counties = findCounties(query, cicozip)
    let county = null

    if (counties.length == 1) {
      county = counties[0]
    }

    this.setState({ counties, county, query: startCase(query) })

    if (county) {
      this.loadData(county.county)
    }
  }

  loadData = (county) => {
    CourtsService.findCourthousesByCounty(county).then((courthouses) => {
      this.setState({ courthouses })
    })
    CourtsService.findCourtsByCounty(county).then((courts) => {
      if (!isEmpty(courts)) {
        this.setState({ courts })
      } else {
        this.setState({courts: [
          {county: county, noResults: true}
        ]})
      }
    })
  }

  selectCounty = (county) => {
    this.setState({ county })
    this.loadData(county.county)
  }

  render () {
    const { courthouses, courts, counties, county, query } = this.state
    const multipleMatches = counties && counties.length > 1

    if (county) {
      // Main interface to show county courthouses
      return (
        <div className="container">
          <Search courthouses={courthouses} courts={courts} county={county.county} searchText={query} zipCode={county.zip}/>
        </div>
      )
    }

    if (multipleMatches) {
      // More than one county available to choose from
      return (
        <div className="container">
          <ChoseCounty counties={counties} onCountySelect={this.selectCounty} searchText={query} />
        </div>
      )
    } else {
      if (isEmpty(query)) {
        // The query was blank, so this is the "initial" state
        return (
          <div className="container">
            <p className="no-results-message">
              Please enter a 5-digit California zip code (e.g. 92110) or city name (e.g. Oakland).
            </p>
          </div>
        )
      } else {
        // Nothing was found in the search
        return (
          <div className="container">
            <p className="no-results-message">
              We’re sorry, this search does not match any California locations in our database.<br/>Please enter a 5-digit California zip code (e.g. 92110) or city name (e.g. Oakland).
            </p>
          </div>
        )
      }
    }
  }
}

export default App
