import React, { Component } from 'react'

import SearchResult from '../components/organisms/SearchResult'
import { getMattersServedOptions, applyFilters } from '../utils/courthouses'
import MattersServedModal from '../components/organisms/MattersServedModal'
import CourtExceptionsModal from '../components/organisms/CourtExceptionsModal'

function Search({ total, courthouses, matterServedOptions, onFilterChange, onMattersServedClick, onCloseCourtExceptionsClick, courtList, searchText, zipCode }) {
  return (
    <React.Fragment>
      { /* <SearchHeader /> */ }
      <SearchResult
        total={total}
        matterServedOptions={matterServedOptions}
        items={courthouses}
        onFilterChange={onFilterChange}
        onMattersServedClick={onMattersServedClick}
        onCloseCourtExceptionsClick={onCloseCourtExceptionsClick}
        courtList={courtList}
        searchText={searchText}
        zipCode={zipCode}
      />
    </React.Fragment>
  )
}

class SearchContainer extends Component {

  constructor(props) {
    super(props)
    this.state = {
      filters: {},
      isMattersServedModalOpen: false
    }
  }

  handleFilterChanged = ({ values }) => {
    this.setState({ filters: values })
  }

  handleOpenMattersServedModal = () => {
    this.setState({ isMattersServedModalOpen: true })
  }

  handleCloseMattersServedModal = () => {
    this.setState({ isMattersServedModalOpen: false })
  }

  handleOpenCourtExceptionsModal = () => {
    this.setState({ isCourtExceptionsModalOpen: true })
  }

  handleCloseCourtExceptionsModal = () => {
    this.setState({ isCourtExceptionsModalOpen: false })
  }

  render() {
    const { courthouses, courts, county, searchText, zipCode } = this.props
    const { filters, isMattersServedModalOpen, isCourtExceptionsModalOpen } = this.state
    const displayCourthouses = applyFilters(courthouses, filters)

    return (
      <React.Fragment>
        <Search
          total={courthouses && courthouses.length}
          matterServedOptions={getMattersServedOptions(courthouses)}
          courthouses={displayCourthouses}
          onFilterChange={this.handleFilterChanged}
          onMattersServedClick={this.handleOpenMattersServedModal}
          onCloseCourtExceptionsClick={this.handleOpenCourtExceptionsModal}
          courtList={courts}
          searchText={searchText}
          zipCode={zipCode}
        />
        <MattersServedModal
          isOpen={isMattersServedModalOpen}
          onClose={this.handleCloseMattersServedModal}
        />
        <CourtExceptionsModal
          isOpen={isCourtExceptionsModalOpen}
          onClose={this.handleCloseCourtExceptionsModal}
        />
      </React.Fragment>
    )
  }
}

export default SearchContainer
