export const sendAnalyticsClick = (category, label) => {
  global.gtag('event', 'click', {
    'event_category': category,
    'event_label': label
  })
}

export const startScrollTracking = () => {
  const percentages = [
    10,
    25,
    50,
    75
  ] // 100 is handled separately

  var percentageHit = {}

  window.addEventListener('scroll', () => {
    requestAnimationFrame(() => {
      const windowHeight = window.innerHeight
      const checkY = window.scrollY + (windowHeight / 2)
      const bodyHeight = document.body.offsetHeight
      if (!percentageHit[100] && checkY >= bodyHeight - (windowHeight / 2)) {
        percentageHit[100] = true
        global.gtag('event', '100%', {
          'event_category': 'Scroll Tracking',
          'event_label': window.location.search
        })
      } else {
        percentages.forEach(percentage => {
          if (!percentageHit[percentage]) {
            if (checkY > bodyHeight * (percentage / 100)) {
              percentageHit[percentage] = true
              global.gtag('event', `${percentage}%`, {
                'event_category': 'Scroll Tracking',
                'event_label': window.location.search
              })
            }
          }
        })
      }
    })
  })
}