import axios from 'axios'

const buildSheetsUrl = (sheetId, page,  key) => {
  return `https://sheets.googleapis.com/v4/spreadsheets/${sheetId}/values/${page}?key=${key}`
}

const googleSheets = {
  async getSheet(id, page) {
    const url = buildSheetsUrl(id, page, process.env.REACT_APP_GOOGLE_API_KEY)
    try {
      let response = await axios(url)
      let jsondata = response.data
      return jsondata
    } catch(err) {
      // catches errors
      console.error(err)
      console.error(err.response.data.error)
      alert("I'm sorry, I'm having trouble connecting to the remote database. Please try again later.")
    }
  }
}


export default googleSheets
