import {
  filter,
  includes,
  uniq,
  map,
  flow,
  trim,
  capitalize,
  isEmpty,
  flatten,
  toLower,
  uniqBy
} from 'lodash'

export const getMattersServedOptions = (items) => {
  const allOptions = uniq(flatten(map(items, 'matters_served')))
  return filter(allOptions, (item) => !isEmpty(item))
}

export const filterByMatterServed = (courthouses, matterServed) => {
  return filter(courthouses, (c) => includes(c.matters_served, matterServed))
}

export const filterBySelfHelp = (courthouses) => {
  return filter(courthouses, (c) => c.has_in_person_help)
}

export const findCounties = (query, list) => {
  if (!query) { return [] }

  const regex = new RegExp(`${query}`)
  const entries = filter(list, ({ city, county, zip }) => (
    regex.test(toLower(city)) || regex.test(toLower(county)) || regex.test(zip)
  ))

  return uniqBy(entries, ({ county, zip}) => county)
}

export const cleanQuery = (query) => {
  let queryCleaned = toLower(trim(query))

  const queryCleanup = queryCleaned.match(/(.*?)[,|+]/) // strip off City,CA or 95401+1234
  if (queryCleanup != null) {
    queryCleaned = trim(queryCleanup[1])
  }

  return queryCleaned
}

export const applyFilters = (courthouses, filters) => {
  let displayCourthouses = courthouses

  if (filters.matterServed) {
    displayCourthouses = filterByMatterServed(courthouses, filters.matterServed)
  }

  if (filters.selfHelp) {
    displayCourthouses = filterBySelfHelp(displayCourthouses)
  }

  return displayCourthouses
}


export const getGoogleMapURL = (address) => {
  return encodeURI(`https://www.google.com/maps/search/?api=1&query=${address}`)
}

export const getGoolgeMapImageUrl = ({ center, key, zoom, size, maptype, markers }) => {
  const url = 'https://maps.googleapis.com/maps/api/staticmap'

  return encodeURI(`${url}?center=${center}&key=${key}&zoom=${zoom}&size=${size}&maptype=${maptype}&markers=${markers}`)
}

export const getGoolgeMapImageUrlForAddress = (address) => {
  const markers = `color:blue|label:C|${address}`
  return getGoolgeMapImageUrl({ center: address, key: process.env.REACT_APP_GOOGLE_API_KEY, zoom: 13, size: '600x300', maptype: 'roadmap', markers: markers })
}

export const getMapImageUrl = (courthouse) => {
  let map_image_filename = `placeholder-map.png`;

  if (!isEmpty(courthouse.map)) {
    map_image_filename = courthouse.map + '.jpg';
  }

  return `maps/` + map_image_filename;
}
