import React, { Component } from 'react'
import { Form, RadioGroup, Radio } from 'informed';
import { isEmpty } from 'lodash'

function countyToId(county) {
  return county.replace(/\s/g, '-');
}

function ChoseCounty({ counties, onCountySelect, searchText }) {
  if (!counties)
    return null

  return (
    <React.Fragment>
      <div className="top-alert" role="alert" aria-atomic="true">
        <h2>
          The search term <strong>{searchText}</strong> falls into multiple counties. Please select a county.
        </h2>
      </div>
      <Form id="choose-county">
        <RadioGroup field="gender">
          <ul className='list-unstyled'>
            {counties.map((v) => (
              <li onClick={() => onCountySelect(v) } className='chose-county'>
                <Radio value="county" id={countyToId(v.county)} />
                <label for={countyToId(v.county)}>{v.county}</label>
              </li>
            ))}
          </ul>
          </RadioGroup>
      </Form>
    </React.Fragment>
  )
}

export default ChoseCounty
