import React from 'react'

import { getGoogleMapURL } from '../../../utils/courthouses'
import { getMapImageUrl } from '../../../utils/courthouses'
import { sendAnalyticsClick } from '../../../utils/analytics'

function Image({ item }) {
  const mapURL = getGoogleMapURL(item.address)
  const style = {
    position: 'absolute',
    width: '100%',
    height: '100%',
    backgroundImage : `url('${getMapImageUrl(item)}')`,
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '50% 50%'
  }
  return (
    <div className="image-wrapper">
      <div className="map-and-attribution">
        <a aria-label={"google map of " + item.courthouse_name + " courthouse"} style={{ 'text-decoration': 'none' }} target="_blank" rel="noopener noreferrer" href={mapURL} onClick={() => sendAnalyticsClick('Outbound Link - Map', mapURL)}>
          <div style={style}></div>
        </a>
        <div className="osm-attribution">© <a aria-label="OpenStreetMap copyright" target="_blank" rel="noopener noreferrer" href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors</div>
      </div>
      { item.has_in_person_help &&
        (
          // This link is the same as the map link above, so it does not need to be reachable via tab
          <a tabindex="-1" style={{ 'text-decoration': 'none' }} target="_blank" rel="noopener noreferrer" href={mapURL} onClick={() => sendAnalyticsClick('Outbound Link - Map', mapURL)}>
            <div className='image-label'>Location has a Self-Help Center</div>
          </a>
        )
      }
    </div>
  )
}

export default Image
