import React from 'react';

import ResultSummary from '../atoms/ResultSummary'
import TopMessage from '../atoms/TopMessage'
import Filter from './FilterNav'
import ResultList from './ResultList'

function SearchResult({ total, items, onFilterChange, matterServedOptions, onMattersServedClick, onCloseCourtExceptionsClick, courtList, searchText, zipCode }) {

  return (
    <React.Fragment>
      { courtList && <ResultSummary courts={courtList} total={total} searchText={searchText} zipCode={zipCode} onCloseCourtExceptionsClick={onCloseCourtExceptionsClick} /> }
      { !total && <TopMessage courts={courtList} /> }
      { total > 1 && <Filter options={matterServedOptions} onChange={onFilterChange} onMattersServedClick={onMattersServedClick} /> }
      <ResultList items={items} onMattersServedClick={onMattersServedClick} />
    </React.Fragment>
  )
}

export default SearchResult
