import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { map, capitalize, startCase, isEmpty } from 'lodash'
import Autolinker from 'autolinker'
import { sendAnalyticsClick } from '../../utils/analytics'
import { featureFlags } from '../../featureFlags'

function CourtLink({ link, label, analyticsCategory }) {
  return (
    <li key={label}><FontAwesomeIcon icon={['fas', 'landmark']} color="#1E70B3" title="Courthouse Icon" />
      <a target="_blank" rel="noopener noreferrer" href={link} onClick={event => sendAnalyticsClick(analyticsCategory, event.target.href)}>{label}</a>
    </li>
  )
}

function CourtLinks({ court }) {
  const { county, website_url, self_help_url } = court
  return (
    <React.Fragment>
      <CourtLink link={website_url} label={`${startCase(county)} County Superior Court Website`} analyticsCategory="Outbound Link - Court" />
      <CourtLink link={self_help_url} label={`Self-Help Services from the Court`} analyticsCategory="Outbound Link - Self Help" />
    </React.Fragment>
  )
}

function CovidAlert({ court }) {
  return (
    <React.Fragment>
      <div class="usa-alert usa-alert--info" role="alert">
        <div class="usa-alert__body">
          <h3 class="usa-alert__heading">COVID-19 and Court Services</h3>
          <p class="usa-alert__text">Courts have reduced services due to health and safety guidelines.</p>
          <p class="usa-alert__text">For up-to-date information, go to <a target="_blank" href={court.website_url} onClick={event => sendAnalyticsClick("Outbound Link - Covid Alert", event.target.href)}>your court's main website</a>.</p>
        </div>
      </div>
      <br/>
    </React.Fragment>
  )
}

function ResultSummary({ courts, total, searchText, zipCode, onCloseCourtExceptionsClick }) {
  if (!isEmpty(courts)) {
    const court = courts[0]
    const { county, matters, noResults } = court

    const mattersWithLinks = Autolinker.link(matters || "", { stripPrefix: false })

    if (!noResults) {
      return (
        <div className="top-alert" role="alert" aria-atomic="true">
          { featureFlags.covidMode ? CovidAlert({ court }) : '' }
          <h2>The jurisdiction for <strong>{searchText}</strong> is <strong>{startCase(county)} County Superior Court</strong></h2>
          { total > 1 && <p className='h5'>There are <strong>{total}</strong> Courthouse Locations.</p> }
          <p className="mb-2" dangerouslySetInnerHTML={{__html: mattersWithLinks}} />
          <p className='top-alert-note'><a href='#' onClick={onCloseCourtExceptionsClick}>When not to go to your county’s court</a></p>
          <ul className='list-unstyled'>
            <CourtLinks court={court} key={court.county}/>
          </ul>
        </div>
      )
    } else {
      return (
        <div className="top-alert" role="alert" aria-atomic="true">
          <h2>The jurisdiction for <strong>{searchText}</strong> is <strong>{startCase(county)} County Superior Court</strong></h2>
        </div>
      )
    }
  }
}

export default ResultSummary
