import React from 'react'
import { isEmpty } from 'lodash'

function TopMessage({ courts }) {
  if (!isEmpty(courts)) {
    return (
      <p className='h4'>Sorry, we currently do not have courthouse data for this jurisdiction. Please try another search.</p>
    )
  } else {
    return (
      <p><br/><small>Searching…</small></p>
    )
  }
}

export default TopMessage
